				} else if (controlId === 'CallPollice') {
					//一键报警
					uni.showToast({
						title: '报警',
						icon: 'none',
					});
				} else if (controlId === 'Big') {
					//放大
					if (this.scale < 18) {
						this.scale = this.scale + 1;
					}
				} else if (controlId === 'Small') {
